      h2 3.4.	Alimentación, hidratación y bienestar en recorridos turísticos
    
    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La alimentación y la hidratación durante un recorrido turístico son factores esenciales para mantener la energía, prevenir emergencias de salud y garantizar una experiencia segura y agradable para los visitantes. De acuerdo con la Organización Mundial del Turismo (OMT, 2019), el bienestar del turista debe considerarse como parte integral de la calidad del servicio, incluyendo condiciones físicas, emocionales y culturales adecuadas.
    
    separador

    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Las Leyes 1558 de 2012 y 300 de 1996 (Ley General de Turismo) establecen que la actividad turística debe fortalecer la identidad cultural y la sostenibilidad ambiental, principios que se reflejan directamente en la gestión alimentaria responsable. De esta manera, la alimentación y la hidratación se integran al concepto de bienestar integral del visitante, promoviendo un turismo que nutre el cuerpo, inspira el espíritu y honra la cultura y el territorio que lo acoge. Para ampliar este aspecto, a continuación se incluye la tabla 7, que hace un paralelo entre la consideración general y los aspectos a tener en cuenta en entornos étnicos y sostenibles:

    br
    .row.justify-content-center(data-aos="fade-left")
