    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La alimentación debe reflejar la soberanía alimentaria y la memoria culinaria de la comunidad anfitriona. Es fundamental incorporar platos tradicionales, explicando su origen, su significado cultural, el método de preparación y su conexión con los ciclos naturales del territorio. De esta manera, cada comida se convierte en un acto pedagógico y de preservación cultural, donde el visitante aprende y valora la relación ancestral entre alimentación, territorio y espiritualidad. Asimismo, se deben respetar las restricciones alimentarias de los visitantes, incluyendo dietas especiales, alergias o preferencias éticas, mediante opciones previamente concertadas y elaboradas con ingredientes locales que cumplan con dichos requerimientos.


